import React from "react";
import "./Search.scss";
import {connect} from "react-redux";
import {setTrackGiftsSearch} from "../../TrackGiftsAction";
import {getTrackGiftsProduct} from "../../../../utils/commonFunction";

class Search extends React.Component {
  inputRef = React.createRef()
  inputContainer = React.createRef()

  applySearch = () => {
    let searchState = {...this.props.stateTrackGiftsSearch}
    searchState.searched = true
    getTrackGiftsProduct(true, this.props.stateTrackGiftsFilters, this.props.stateTrackGiftsProducts, this.props.stateTrackGiftsPagination, this.props.stateTrackGiftsView, this.props.stateRegistryInfo, this.props.stateTrackGiftsSearch, this.props.dispatch)
    this.props.dispatch(setTrackGiftsSearch(searchState))
  }

  searchInputChangeHandler = async () => {
    if (this.inputRef.current && this.inputContainer.current) {
      let searchInput = this.inputRef.current
      if (searchInput.value.length > 0) {
        if (!this.inputContainer.current.classList.contains("track-gifts-search-container-focus")) {
          //Adding class to increase width
          this.inputContainer.current.classList.add("track-gifts-search-container-focus")
        }
      }

      await this.props.dispatch(setTrackGiftsSearch({
        searched: false,
        searchText: searchInput.value
      }))
    }
  }

  searchInputFocusHandler = () => {
    if (this.inputContainer.current) {
      if (!this.inputContainer.current.classList.contains("track-gifts-search-container-focus")) {
        //Adding class to increase width
        this.inputContainer.current.classList.add("track-gifts-search-container-focus")
      }
    }
  }

  searchInputBlurHandler = () => {
    if (this.inputRef.current && this.inputContainer.current) {
      let searchInput = this.inputRef.current
      if (searchInput.value.length <= 0) {
        if (this.inputContainer.current.classList.contains("track-gifts-search-container-focus")) {
          //Removing class to decrease width
          this.inputContainer.current.classList.remove("track-gifts-search-container-focus")
        }
      }

      if (searchInput.value.length >= 3 && !this.props.stateTrackGiftsSearch.searched) {
        this.applySearch()
      }
    }
  }

  clearClickHandler = async ev => {
    ev.preventDefault()
    await this.props.dispatch(setTrackGiftsSearch({
      searched: false,
      searchText: ""
    }))
    if (this.inputRef.current) {
      this.inputRef.current.focus()
    }

    this.applySearch()
  }

  searchClickHandler = async ev => {
    ev.preventDefault()
    this.applySearch()
  }

  render() {
    return (
      <div className="ml-auto track-gifts-search-container" ref={this.inputContainer}>
        <input
          type="text"
          ref={this.inputRef}
          placeholder="Search"
          value={this.props.stateTrackGiftsSearch.searchText}
          onChange={this.searchInputChangeHandler}
          onFocus={this.searchInputFocusHandler}
          onBlur={this.searchInputBlurHandler}
          className={["m-a-unset-line-height track-gifts-search-input pl-6 text-body py-1 w-100 mw-100", this.props.stateTrackGiftsSearch.searched && this.props.stateTrackGiftsSearch.searchText.length > 0 ? "pr-10": "pr-12"].join(" ")}
        />
        <i className="icon-search track-gifts-search-icon"/>
        {!this.props.stateTrackGiftsSearch.searched && this.props.stateTrackGiftsSearch.searchText.length > 0 ?
          <a className="track-gifts-search-input-link text-body" href="#"
             onClick={(ev) => this.searchClickHandler(ev)}>Search</a>
          : null}
        {this.props.stateTrackGiftsSearch.searched && this.props.stateTrackGiftsSearch.searchText.length > 0 ?
          <a className="track-gifts-search-input-link text-body" href="#"
             onClick={(ev) => this.clearClickHandler(ev)}>Clear</a>
          : null}
      </div>
    );
  }
}

const mapStateToProps = state => ({
  stateTrackGiftsSearch: state.trackGifts.search,
  stateTrackGiftsFilters: state.trackGifts.filters,
  stateTrackGiftsView: state.trackGifts.activeView,
  stateTrackGiftsProducts: state.trackGifts.products,
  stateTrackGiftsPagination: state.trackGifts.paginationInfo,
  stateRegistryInfo: state.registry.info
})

export default connect(
  mapStateToProps
)(Search);