import React, { useState } from 'react'
import "./GuestViewCard.scss";
import {
	sortableHandle,
} from 'react-sortable-hoc';
import { useSelector, useDispatch } from 'react-redux';
import Button from "../../commons/components/button-component/ButtonComponent";
import GiftStatusPill from "../../commons/components/gift-status-pill/GiftStatusPill";
import GiveMAGiftModel from "modules/commons/components/give-MA-gift-model/GiveMAGiftModel";
import { clearAlreadyBoughtProductInfo, clearCashContributedAlreadyInfo } from '../GuestViewAction';
import { M_A_STORE_NAME } from 'utils/constants';
import GiveGiftFromAnotherStore from "modules/commons/components/give-gift-from-another-store/GiveGiftFromAnotherStore";


const GuestViewCard = (props) => {

	const dispatch = useDispatch();
	const stateFilterApplied = useSelector(state => state.viewRegistry.viewFilterApplied.childFilter)
	const [showMAGiftmodel, setMAGiftModel] = useState(false)
	const [showAnotherStoreGiftmodel, setAnotherStoreGiftmodel] = useState(false)
	const [isGroupGift, setIsGroupGift] = useState(false)
	const [isCashAcceptedOnly, setIsCashAcceptedOnly] = useState(false)
	const [modelTitle, setModelTitle] = useState('')

	const modalCloseHandler = () => {
		dispatch(clearAlreadyBoughtProductInfo())
		dispatch(clearCashContributedAlreadyInfo())
		setMAGiftModel(false)
		setAnotherStoreGiftmodel(false)
	}

	// Handle drag
	// TODO: Change Draggable icon
	const DragHandle = sortableHandle(() => <i
		className={["ml-4 drag-icon m-a-popover m-a-popover-top-right", (props.disabledDrag || stateFilterApplied.length !== 0) ? "disabled-drag" : ""].join(" ")}>
		<div className="m-a-popover-content drag-drop-popover w-auto px-3 py-2">
			<p className="text-sm font-weight-normal m-a-popover-content-desc">Rearrange</p>
		</div>
	</i>);

	const openModelHandler = () => {
		// TODO: Conditions for different data model
		// For ma product which is not Grop Gift
		if (props.data.item.store.storeName === M_A_STORE_NAME && !props.data.isGroupGift) {
			setMAGiftModel(true)
		}
		// For Group Gift
		else if (props.data.isGroupGift) {
			setIsGroupGift(true)
			setMAGiftModel(true)
		}
		else if (props.data.isCashAcceptedOnly) {
			setIsCashAcceptedOnly(true)
			setMAGiftModel(true)
		}
		else if (props.data.item.store.storeName !== M_A_STORE_NAME) {
			setAnotherStoreGiftmodel(true)
		}
		setModelTitle('Give this gift')
		// modelTitle = 'Give This Gift'
	}

	return (
		<>
			{!props.data.isPurchased ?
				<>
					<div className="added-card py-5 pr-2">
						<div className="row pl-3">
							<div className="col-2 guest-card-image">
								{props.data?.item.imageUrl ?
									<img className="product-img d-block mx-auto" src={props.data?.item.imageUrl} alt={props.data?.title} />
									:
									<div className="added-card-no-product-image" />
								}
								{props.data.mostWanted ?
									<div
										className="d-flex align-items-center guest-must-have added-gift-must-have-btn px-1 added-gift-must-have-active">
										<i className="icon mr-1 d-block heart-fill" />
										<p className="text-sm added-gift-must-have-text">Must Have</p>
									</div>
									: <></>}
							</div>
							<div className="col">
								{/* <div className="text-sm category">{props.data.registryCategories[0].category.categoryName}</div> */}
								<h2>{props.data.title}</h2>
								<div className="d-flex mt-2">
									<div className="">${props.data.item.price}</div>
									{/*/!* TODO: Add Strikeout price dynamic *!/*/}
									{/*<div className="text-sm my-auto ml-1 strick-price">$257.99</div>*/}
									{/* {props.data.quantity > 1 ?
								<div className="ml-4 text-body d-flex">
									Qty:{props.data.quantity}
									<div className="quantity-mul-text"> (${props.data.item.price} * {props.data.quantity})</div>
								</div> : <></>} */}
								</div>
								<div className="d-flex mt-3 flex-wrap">
									{props.data.quantityStatus.purchased_quantity && !props.data.item.isCashFund ?
										<GiftStatusPill className="mr-1" variant="purchased" text={props.data.quantityStatus.purchased_quantity + " Purchased"} />
										: null}
									{props.data.quantityStatus.reserved_quantity && !props.data.item.isCashFund ?
										<GiftStatusPill className="mr-1" variant="reserved" text={props.data.quantityStatus.reserved_quantity + " Reserved"} />
										: null}
									{props.data.quantityStatus.still_needed && !props.data.item.isCashFund ?
										<GiftStatusPill className="mr-1" variant="default" text={`${props.data.quantityStatus.still_needed} ${props.data.quantityStatus.purchased_quantity === 0 ? " Requested" : " Still Needed"}`} />
										: null}
									{props.data.isGroupGift ?
										<>
											<div className="guest-toggle-pills px-1 mr-3 d-flex align-items-center mt-md-2 mt-lg-0">
												<i className="guest-icon-active mr-1" />
												<span className="text-sm guest-icon-text">Group Gift</span>
											</div>
											{props.data.item.hideTotal ? null :
												<div className="mt-md-2 mt-lg-0">
													<span className="text-sm font-medium received-amount">Received ${props.data.purchasedRegistryItem.length ? Math.round((props.data.purchasedRegistryItem[0].groupGift.amountReceived % props.data.item.price) * 100) / 100 : 0} </span>
													<span className="text-sm total-amount">of ${Math.round(props.data.item.price * props.data.quantityStatus.still_needed * 100) / 100}</span>
													<div className="group-gift-background">
														<div className="group-gift-progress mw-100" style={{ width: `${props.data.purchasedRegistryItem.length ? (props.data.purchasedRegistryItem[0].groupGift.amountReceived % props.data.item.price * 100) / (props.data.item.price * props.data.quantity) : 0}%` }} />
													</div>
												</div>
											}
										</>
										: null}
								</div>
							</div>
							<div className={[
								props.draggable ? "col-4" : "col-3",
							].join(" ")}
							>
								<div className="d-flex align-items-baseline justify-content-end mr-1">
									<Button variant="secondary" className="give-this-gift-btn font-medium text-body py-1 px-3 mr-3"
										onClick={() => openModelHandler()}
									>Give This Gift</Button>
								</div>
							</div>
						</div>
					</div>
					{/* TODO: change category */}
					<div className="row">
						<div className="col">
							{props.data.registryCategories.length > 1 ?
								props.data.registryCategories.map(category =>
									props.data.registryCategories[0].id !== category.id ?
										<div className="text-body font-medium p-6 added-card-other-category">
											You have added {props.data.item.quantity > 1 ? props.data.item.quantity : null} {category.category.parentCategory && category.category.parentCategory.parentCategory !== null ? category.category.parentCategory.categoryName + " (" + category.category.categoryName + ")" : category.category.categoryName} as part of <a className="title-link" tabIndex="true">{props.data.title}</a>
										</div>
										: null
								)
								: null}
						</div>
					</div>
				</> :
				<div className="guest-purchased-card py-3 pl-6">
					<p className="guest-purchased-card-title font-medium">{props.data.title}</p>
					{props.data.quantityStatus.purchased_quantity ?
						<GiftStatusPill className="mr-1" variant="purchased" text={props.data.quantityStatus.purchased_quantity + " Purchased"} />
						: null}
				</div>
			}
			{showMAGiftmodel ?
				<GiveMAGiftModel
					show={showMAGiftmodel}
					close={modalCloseHandler}
					title={modelTitle}
					data={props.data}
					isGroupGift={isGroupGift}
					isCashAcceptedOnly={isCashAcceptedOnly}
					updateGuestViewProduct={props.updateGuestViewProduct}
				/> : null}

			{showAnotherStoreGiftmodel ?
				<GiveGiftFromAnotherStore
					show={showAnotherStoreGiftmodel}
					close={modalCloseHandler}
					title={modelTitle}
					data={props.data}
					updateGuestViewProduct={props.updateGuestViewProduct}
				/> : null}
		</>
	)
}

export default GuestViewCard